@import 'mixins';
@import 'variables';

.paragraphs {
    line-height: 3rem;
    & > * {
        transition: 0.6s;
    }

    .emphasized {
        color: darken($primary-color, 65%);
        font-size: 1.75rem;
    }

    .paragraph-content {
        // transition: 300s linear;
        max-height: 100rem;
        margin-bottom: 2rem;
    }
    .paragraph-content.collapsed {
        // transition: 0.6s linear;
        max-height: 4.5rem;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.123) 100%);
        border-radius: 0 0 10px 10px;
        // border-bottom: 1px solid rgba(0, 0, 0, 0.596);
        //box-shadow: 0 7px 0 10px rgba(0, 0, 0, 0.479);
        box-shadow: 0 5px 5px -2px #888;
        overflow: hidden;
    }

    a {
        color: rgb(255, 81, 0);
        font-weight: 700;
    }
}